.footer {
    padding: 25px 0;
    border-top: 1px solid $light-gray;

    &-wrapper {
        display: flex;
        align-items: center;

        @media screen and (max-width: $small) {
            flex-direction: column; } }

    &__descr {
        font-size: $fs14;
        @media screen and (max-width: $small) {
            padding: 0 15px; } }

    &__col {
        width: calc(25% - 40px);
        margin-right: 40px;

        &--small {
            width: 15%;

            @media screen and (max-width: $small) {
                width: 100%; } }

        &--wide {
            width: 40%;

            @media screen and (max-width: $small) {
                width: 100%; } }

        @media screen and (max-width: $small) {
            // display: flex
            justify-content: center;
            text-align: center;
            width: 100%;
            margin-right: 0;

            &:not(:last-child) {
                margin-bottom: 25px; } } }

    &__phone,
    &__address {
        display: flex;

        &-wrapper {
            display: flex;
            align-items: center;

            @media screen and (max-width: $small) {
                flex-direction: column; } } }

    &__address-wrapper {
        margin-bottom: 15px; }

    &__icon {
        margin-right: 10px;

        @media screen and (max-width: $small) {
            margin-right: 0;
            margin-bottom: 5px; } } }
