.contact-us {
    padding: 100px 0 150px;

    @media screen and (max-width: $small) {
        padding: 20px 15px 80px; }

    &__title {
        text-align: center;
        margin-bottom: 5px; }

    &__descr {
        text-align: center;
        margin-bottom: 55px;

        @media screen and (max-width: $small) {
            margin-bottom: 30px;
            font-size: $fs18; } }

    &__wrapper {
        display: flex;
        flex-direction: column;
        background-color: $white;

        padding: 40px 35px;
        border-radius: $border-radius;

        width: 57%;
        box-shadow: $box-shadow;

        @media screen and (max-width: $medium) {
            width: 80%; }

        @media screen and (max-width: $small) {
            width: 100%;
            padding: 20px 15px; } }

    &__label {
        width: calc(50% - 20px);
        position: relative;

        @media screen and (max-width: $small) {
            width: 100%; }

        &:last-child {
            width: 100%; }

        &-text {
            color: $gray;
            font-size: $fs14;

            position: absolute;
            left: 40px;
            top: -7px;
            z-index: 3;

            transition: $transition;

            // Background layout to provide exact design
            // compared with origin design

            &:before {
                display: block;
                content: "";

                height: 50%;
                width: 100%;

                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: -1;

                background-color: lighten($light-gray, 10%); } } }

    &__input {
        width: calc(100% - 20px);
        position: relative;

        padding: 15px;
        border-radius: $button-border-radius;
        border: 1px solid $light-gray;

        background-color: lighten($light-gray, 10%);
        transition: $transition;

        &:not(:last-child) {
            margin: 0 10px 40px; }

        &:last-child {
            margin: 0 10px 35px; }

        @media screen and (max-width: $small) {
            &:not(:last-child) {
                margin: 0 10px 20px; }

            &:last-child {
                margin: 0 10px 15px; } }

        &-group {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            margin: 0 -10px;
            width: 100%; }

        &.error {
            border-color: $red; } }

    &__input:focus + &__label-text {
        top: -20px;

        &:before {
            background-color: rgba(0,0,0,0); } }

    label.error {
        position: absolute;
        bottom: 20px;
        left: 40px;
        font-size: $fs14;
        transition: $transition; }

    &__input.error ~ &__label-text,
    &__input.error ~ label.error {
        color: $red; }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 100%; } }
