.showcase {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: calc(100% / 3);

    @media screen and (max-width: $small) {
        width: 100%;

        &:not(:last-child) {
            margin-bottom: 40px; } }

    &__title {
        @media screen and (max-width: $small) {
            margin-top: 25px; } }

    &__descr {
        text-align: center;
        font-size: $fs18;

        width: 70%;

        @media screen and (max-width: $small) {
            margin-top: 5px; } }

    &s {
        display: flex;
        justify-content: space-between;

        @media screen and (max-width: $small) {
            flex-direction: column; }

        &__title {
            margin-bottom: 10px;

            @media screen and (max-width: $small) {
                margin-bottom: 5px; } }

        &__descr {
            width: 47.578125%;
            margin-bottom: 40px;
            text-align: center;

            @media screen and (max-width: $small) {
                padding: 0 15px;
                width: 100%;
                font-size: $fs20; } }

        &-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            padding: 107px 0 127px;

            @media screen and (max-width: $small) {
                padding: 56px 0 76px; } } } }
