*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font: 1.6rem/1 "Source Sans Pro", Arial, Helvetica, sans-serif;
  overflow-x: hidden;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a:visited,
a:link,
a:active {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

button,
input {
  border: none;
  background: none;
  outline: none;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  width: 100%;
  margin: 0 auto;
}

.red {
  font-weight: bold;
  color: #bc2026;
}

.complex-bg {
  display: flex;
  flex-direction: column;
  background: url(../assets/footer-bg.png) no-repeat center top;
  background-size: cover;
}

h1,
h2 {
  font-family: "Spectral", Arial, Helvetica, sans-serif;
  font-weight: bold;
  margin: 0;
  text-align: center;
}

h1 {
  font-size: 5.6rem;
}

@media screen and (max-width: 650px) {
  h1 {
    font-size: 3.6rem;
  }
}

h2 {
  font-size: 3.2rem;
}

@media screen and (max-width: 650px) {
  h2 {
    font-size: 2.8rem;
  }
}

h3 {
  font-size: 2rem;
}

p {
  margin: 0;
  padding: 0;
  line-height: 1.33;
  font-size: 2.4rem;
  color: #504f5b;
}

.button {
  border-radius: 28px;
  padding: 1.7rem;
  box-shadow: 1px 4px 10px 0 rgba(50, 9, 9, 0.3);
  background-image: linear-gradient(101deg, #e23e57, #cf2f3e 51%, #bc2026);
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  font-size: 1.6rem;
}

.button:hover {
  transform: scale(0.98);
}

.button:active {
  transform: scale(0.96);
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  text-align: center;
  padding-top: 35px;
}

.header__logo {
  height: 50px;
  object-fit: cover;
}

.hero {
  padding: 190px 0 115px;
  position: relative;
  background: url(../assets/hero-bg.png) no-repeat bottom;
  background-size: cover;
}

@media screen and (max-width: 650px) {
  .hero {
    padding: 130px 0 45px;
  }
}

.hero::after, .hero::before {
  display: block;
  content: "";
  position: absolute;
}

@media screen and (max-width: 650px) {
  .hero::after, .hero::before {
    display: none;
  }
}

.hero::after {
  top: 0;
  right: 0;
  width: 375px;
  height: 325px;
}

.hero::before {
  bottom: 0;
  left: 0;
  width: 350px;
  height: 700px;
}

.hero-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero__title {
  text-align: center;
  margin-bottom: 20px;
}

.hero__description {
  text-align: center;
  margin-bottom: 50px;
  width: 45.3125%;
}

@media screen and (max-width: 650px) {
  .hero__description {
    width: 100%;
  }
}

.showcase {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% / 3);
}

@media screen and (max-width: 650px) {
  .showcase {
    width: 100%;
  }
  .showcase:not(:last-child) {
    margin-bottom: 40px;
  }
}

@media screen and (max-width: 650px) {
  .showcase__title {
    margin-top: 25px;
  }
}

.showcase__descr {
  text-align: center;
  font-size: 1.8rem;
  width: 70%;
}

@media screen and (max-width: 650px) {
  .showcase__descr {
    margin-top: 5px;
  }
}

.showcases {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 650px) {
  .showcases {
    flex-direction: column;
  }
}

.showcases__title {
  margin-bottom: 10px;
}

@media screen and (max-width: 650px) {
  .showcases__title {
    margin-bottom: 5px;
  }
}

.showcases__descr {
  width: 47.578125%;
  margin-bottom: 40px;
  text-align: center;
}

@media screen and (max-width: 650px) {
  .showcases__descr {
    padding: 0 15px;
    width: 100%;
    font-size: 2rem;
  }
}

.showcases-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 107px 0 127px;
}

@media screen and (max-width: 650px) {
  .showcases-wrapper {
    padding: 56px 0 76px;
  }
}

.partner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% / 6 - 30px);
  height: 160px;
  box-shadow: 0 7px 15px 0 rgba(17, 17, 28, 0.1);
}

@media screen and (max-width: 1024px) {
  .partner {
    width: calc(100% / 3 - 15px);
  }
}

@media screen and (max-width: 650px) {
  .partner {
    width: calc(100% / 2 - 15px);
    margin: 15px 0;
  }
}

.partners {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

@media screen and (max-width: 650px) {
  .partners {
    margin: -10px 0;
  }
}

.partners-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 165px;
}

.partners__title {
  text-align: center;
  margin-bottom: 45px;
  width: 80%;
}

@media screen and (max-width: 650px) {
  .partners__title {
    width: 100%;
  }
}

.testis-wrapper {
  border-bottom: 1px solid #dddddd;
}

@media screen and (max-width: 650px) {
  .testis-wrapper {
    border-bottom: 0;
  }
}

.testis__descr {
  margin-bottom: 35px;
  font-size: 2.8rem;
  font-style: italic;
  font-family: "Spectral", Arial, Helvetica, sans-serif;
}

@media screen and (max-width: 650px) {
  .testis__descr {
    text-align: center;
    font-size: 2.4rem;
    margin-bottom: 0;
  }
}

.testis__client-info {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .testis__client-info {
    flex-direction: column;
    align-items: center;
  }
}

.testis__company-logo {
  display: inline-block;
  border-right: 1px solid #dddddd;
  padding: 15px 30px 15px 0;
  width: 170px;
  box-sizing: content-box;
}

@media screen and (max-width: 650px) {
  .testis__company-logo {
    border-right: 0;
    padding: 0;
    margin-top: 25px;
  }
}

.testis__company-info {
  display: flex;
  flex-direction: column;
  padding: 15px 0 15px 40px;
}

@media screen and (max-width: 650px) {
  .testis__company-info {
    padding: 0;
    align-items: center;
    margin-top: 25px;
  }
}

.testis__rank {
  font-weight: bold;
  font-size: 1.8rem;
}

.testis__employee {
  display: inline-block;
  font-size: 1.8rem;
  margin-bottom: 5px;
  color: #504f5b;
}

.testimonials-wrapper {
  position: relative;
  padding: 70px 15px;
}

.testimonials-wrapper::before {
  display: block;
  content: "";
  position: absolute;
  top: -42.5px;
  left: 15px;
  width: 100px;
  height: 85px;
  background: url(../assets/quotes.svg) no-repeat center center;
  background-size: contain;
}

.contact-us {
  padding: 100px 0 150px;
}

@media screen and (max-width: 650px) {
  .contact-us {
    padding: 20px 15px 80px;
  }
}

.contact-us__title {
  text-align: center;
  margin-bottom: 5px;
}

.contact-us__descr {
  text-align: center;
  margin-bottom: 55px;
}

@media screen and (max-width: 650px) {
  .contact-us__descr {
    margin-bottom: 30px;
    font-size: 1.8rem;
  }
}

.contact-us__wrapper {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding: 40px 35px;
  border-radius: 5px;
  width: 57%;
  box-shadow: 0 7px 15px 0 rgba(17, 17, 28, 0.1);
}

@media screen and (max-width: 1024px) {
  .contact-us__wrapper {
    width: 80%;
  }
}

@media screen and (max-width: 650px) {
  .contact-us__wrapper {
    width: 100%;
    padding: 20px 15px;
  }
}

.contact-us__label {
  width: calc(50% - 20px);
  position: relative;
}

@media screen and (max-width: 650px) {
  .contact-us__label {
    width: 100%;
  }
}

.contact-us__label:last-child {
  width: 100%;
}

.contact-us__label-text {
  color: #504f5b;
  font-size: 1.4rem;
  position: absolute;
  left: 40px;
  top: -7px;
  z-index: 3;
  transition: all 0.2s ease-in-out;
}

.contact-us__label-text:before {
  display: block;
  content: "";
  height: 50%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-color: #f7f7f7;
}

.contact-us__input {
  width: calc(100% - 20px);
  position: relative;
  padding: 15px;
  border-radius: 28px;
  border: 1px solid #dddddd;
  background-color: #f7f7f7;
  transition: all 0.2s ease-in-out;
}

.contact-us__input:not(:last-child) {
  margin: 0 10px 40px;
}

.contact-us__input:last-child {
  margin: 0 10px 35px;
}

@media screen and (max-width: 650px) {
  .contact-us__input:not(:last-child) {
    margin: 0 10px 20px;
  }
  .contact-us__input:last-child {
    margin: 0 10px 15px;
  }
}

.contact-us__input-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px;
  width: 100%;
}

.contact-us__input.error {
  border-color: #bc2026;
}

.contact-us__input:focus + .contact-us__label-text {
  top: -20px;
}

.contact-us__input:focus + .contact-us__label-text:before {
  background-color: rgba(0, 0, 0, 0);
}

.contact-us label.error {
  position: absolute;
  bottom: 20px;
  left: 40px;
  font-size: 1.4rem;
  transition: all 0.2s ease-in-out;
}

.contact-us__input.error ~ .contact-us__label-text,
.contact-us__input.error ~ label.error {
  color: #bc2026;
}

.contact-us__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.footer {
  padding: 25px 0;
  border-top: 1px solid #dddddd;
}

.footer-wrapper {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .footer-wrapper {
    flex-direction: column;
  }
}

.footer__descr {
  font-size: 1.4rem;
}

@media screen and (max-width: 650px) {
  .footer__descr {
    padding: 0 15px;
  }
}

.footer__col {
  width: calc(25% - 40px);
  margin-right: 40px;
}

.footer__col--small {
  width: 15%;
}

@media screen and (max-width: 650px) {
  .footer__col--small {
    width: 100%;
  }
}

.footer__col--wide {
  width: 40%;
}

@media screen and (max-width: 650px) {
  .footer__col--wide {
    width: 100%;
  }
}

@media screen and (max-width: 650px) {
  .footer__col {
    justify-content: center;
    text-align: center;
    width: 100%;
    margin-right: 0;
  }
  .footer__col:not(:last-child) {
    margin-bottom: 25px;
  }
}

.footer__phone, .footer__address {
  display: flex;
}

.footer__phone-wrapper, .footer__address-wrapper {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .footer__phone-wrapper, .footer__address-wrapper {
    flex-direction: column;
  }
}

.footer__address-wrapper {
  margin-bottom: 15px;
}

.footer__icon {
  margin-right: 10px;
}

@media screen and (max-width: 650px) {
  .footer__icon {
    margin-right: 0;
    margin-bottom: 5px;
  }
}

.slick-list {
  width: 80%;
}

@media screen and (max-width: 650px) {
  .slick-list {
    width: 100%;
  }
}

.slick-arrow {
  position: absolute;
  right: 0;
  top: 0;
  left: unset;
  z-index: 3;
  width: 40px;
  height: 14px;
  transform: none;
  transition: all 0.2s ease-in-out;
}

@media screen and (max-width: 650px) {
  .slick-arrow {
    top: -40px;
  }
}

.slick-arrow:hover {
  opacity: 1;
  background-position: center center;
}

.slick-arrow:before {
  content: "";
}

.slick-prev {
  right: 70px;
}

@media screen and (max-width: 650px) {
  .slick-prev {
    right: 60px;
  }
}

.slick-prev, .slick-prev:focus, .slick-prev:active, .slick-prev:hover {
  background: url(../assets/arrow.svg) no-repeat center center;
  transform: rotate(180deg);
}

.slick-prev:hover {
  transform: rotate(180deg) translateX(10px);
}

.slick-prev.slick-disabled {
  background: url(../assets/arrow-disabled.svg) no-repeat center center;
  transform: rotate(360deg);
}

.slick-next, .slick-next:focus, .slick-next:active, .slick-next:hover {
  background: url(../assets/arrow.svg) no-repeat center center;
}

.slick-next:hover {
  transform: translateX(10px);
}

.slick-next.slick-disabled {
  background: url(../assets/arrow-disabled.svg) no-repeat center center;
  transform: rotate(180deg);
}

.slick-next.slick-disabled:hover {
  transform: rotate(180deg) translateX(15px);
}
